/**
* 案件->票据管理
* @module 案件
* @desc 票据管理表格
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div >
        <div class="search-wrop" >
            <!-- <lj-search-from :onAdd="onAdd" :onDel="buttonCheck('batchdel')?onDel:''" :onImport='onImport'
                            :searchObj="searchObj" @searchList="searchList"/> -->
            <el-form :inline="true" :model="form" :rules="rules" class="demo-form-inline wropcss" ref="ruleForm">
                <el-form-item label=" 领用部门：" prop="collarOrg">
                    <el-select v-model="form.collarOrg" placeholder="请选择部门"  class="marginleft divwidth">
                        <el-option
                            v-for="item in collarOrgList"
                            :key="item.orgId"
                            :label="item.orgName"
                            :value="item.orgId"
                            >
                        </el-option>
                  </el-select>     
                </el-form-item>
                <el-form-item label="领用人：" prop="collarOrgPersoId">
                    <el-select v-model="form.collarOrgPersoId" placeholder="请选择领用人" class="marginleft divwidth">
                        <el-option
                            v-for="item in PersonList"
                            :key="item.userId"
                            :label="item.fullName"
                            :value="item.userId"
                            >
                        </el-option>
                    </el-select>    
                </el-form-item>
                 <el-form-item label="领用时间：" prop="collarTime">
                    <el-date-picker
                        v-model="form.collarTime"
                        type="date"
                        placeholder="选择日期" 
                        class="marginleft divwidth">
                  </el-date-picker>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitForm('ruleForm')" class="marginleft" >领用发票</el-button>
                </el-form-item>
            </el-form>      
        </div>
        <!-- 表格 -->
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}"
                  @selection-change="handleSelectionChange" @row-click="seeFunction" v-loading='loading'
                  border size="mini">
            <el-table-column type="selection" width="55" />
            <el-table-column align="center" label="面额" prop="quotaInvoiceAmount" show-overflow-tooltip width="80"/>
            <el-table-column align="center" label="起讫号" prop="quotaInvoiceNum" show-overflow-tooltip/>
            <el-table-column align="center" label="份数" prop="quotaInvoiceCount" show-overflow-tooltip width="80"/>
            <el-table-column align="center" label="上缴份数" prop="submittedNum" show-overflow-tooltip width="80"/>
            <el-table-column align="center" label="入库日期" prop="createTime" show-overflow-tooltip width="180">
                <template slot-scope="scope">
                    {{scope.row.createTime | formatDate}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="入库状态" prop="quotaStatus" width="100">
                <template slot-scope="scope">
                    {{scope.row.quotaStatus | quotaStatus}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="操作人" prop="createUser" show-overflow-tooltip width="100"/>
            <el-table-column align="center" label="操作" width="80">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link @click.stop="seeFunction(scope.row)" class="el-icon-search btn-css"
                                 type="success" v-if="buttonCheck('see')"></el-link>
                    </el-tooltip>
                </template>
            </el-table-column> 
        </el-table>
        <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total" @getData="getList"
                       class="page-css"
        />

        <!-- 查看 -->
        <seecontent :caseProcessList="caseProcessList" :caseTypeList='caseTypeList' :show.sync="seeObj.show"
                    :title="seeObj.title" :id="seeObj.editId"/>
               
    </div>
</template>

<script>
    import LjPagination from '../../../../../components/LjPagination'
    import LjSearchFrom from '../../../../../components/LjSearchForm'
    import seecontent from "./seecontent";
    import {
        apiCaseQuotaInvoiceBatchDelete,
        apiCaseQuotaInvoiceDelete,
        apiCaseQuotaCollaruserSearch,
        apiCaseQuotaInvoiceSearch,
        apiCaseQuotaCollaruserAdd
    } from "../../../../../api/case";
    import {
       apiOrgSearch,apiUserSearch
    } from "../../../../../api/system";
    import {formatDate,quotaStatus} from "../../../../../until/wordBook";

    export default {
        name:'getuse',
        data() {
            return {
                collarOrgList:[],//部门
                PersonList:[],//领用人
                caseProcessList: [],
                loading: true,
                searchObj: [
                    {type: 'input', name: 'quotaInvoiceNum', label: '发票号码'},
                    {
                        type: 'select', name: 'quotaInvoiceAmount', options: [
                            {value: '10', label: '10'},
                            {value: '20', label: '20'},
                            {value: '50', label: '50'},
                            {value: '100', label: '100'},
                        ], label: '发票面额'
                    },
                ],
                //弹出框数据
                seeObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                caseTypeList: [],
                form: {
                    pageSize: 20,
                    pageNum: 1,
                },
                total: 0,
                list: [],
                idstr: '',
                rules: {
                    collarOrg: [{required: true, message: "请选择部门", trigger: "change"}],
                    collarOrgPersoId: [{required: true, message: "请选择员工", trigger: "change"}],
                    collarTime: [{required: true, message: "请选择领用时间", trigger: "change"}]
                },
            }
        },
        components: {
             LjPagination, LjSearchFrom,seecontent
        },


        methods: {
            //领用发票
             submitForm(formName) {
                //  console.log(buttonCheck('getbill'))
                this.$refs[formName].validate(valid => {
                    if (valid) {
                        if(this.idstr==""){
                            this.$message({
                                type: "warning",
                                message: "请选择要领取的定额发票 !"
                            });
                        }else{
                            this.loading = true;
                            this.form.collarTime = Date.parse(this.form.collarTime);
                            let obj = this.collarOrgList.find((item)=>{
                                return item.orgId === this.form.collarOrg;
                            });
                            this.form.collarOrgName = obj.orgName;//组织名称；
                            let obj2 = this.PersonList.find((item)=>{
                                return item.userId === this.form.collarOrgPersoId;
                            });
                            this.form.collarOrgPerson = obj2.fullName;//人员名称；
                            this.form.quotaIds = this.idstr; //选择票据id
                            apiCaseQuotaCollaruserAdd(this.form).then(res => {
                                this.loading = false;
                                this.$message({
                                    type: "success",
                                    message: "领取发票成功 !"
                                });
                                this.getList();
                                this.form={}
                                this.$nextTick(() => {
                                    this.$refs.ruleForm.clearValidate();
                                });
                            })  
                           
                        }
                    } else {
                        this.$message({
                            type: "warning",
                            message: "请把查询条件填写完整 !"
                        });
                        return false;
                    }
                });
            },
            //批量选择
            handleSelectionChange(val) {
                let idarr = [];
                val.forEach(item => {
                    idarr.push(item.quotaId);
                });
                this.idstr = idarr.join(',')
            },

           

            //查询+搜索
            getList() {
                apiCaseQuotaInvoiceSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                })
            },

            //查看
            seeFunction(row) {
                this.seeObj.editId = row.quotaId;
                this.seeObj.title = '查看';
                this.seeObj.show = true;
            },



            //搜索点击
            searchList(data) {  //data就是接收的this.form数据
                this.loading = true;
                data.pageSize = this.form.pageSize;
                this.form = JSON.parse(JSON.stringify(data));
                this.form.pageNum = 1;
                apiCaseQuotaInvoiceSearch(this.form).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                })
            },
           
        },

        filters: {
            quotaStatus(val) {
                return quotaStatus[val]
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd - hh:mm:ss')
            },
        },
       
        created() {
            this.getList();
            this.$nextTick(() => {
                this.$refs.ruleForm.clearValidate();
            });
            //部门
            apiOrgSearch(this.form).then(res => {
                 this.collarOrgList = res.data;
            });
            //员工
             apiUserSearch(this.form).then(res => {
                 this.PersonList = res.data;
            })

        },
    }
</script>

<style scoped>
    .search-wrop {
        margin-bottom: 5px;
    }

    .btn-css {
        margin: 0 10px
    }

    .page-css {
        margin-top: 10px
    }
    .marginleft{
        margin-right: 15px
    }
    .divwidth{
        width: 150px
    }
</style>
