/**
* 案件->票据管理
* @module 案件
* @desc 票据管理下载上传
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" center top="30px"
                   width="80%">
            <div class="wrop" v-loading="loading">
                <table class="gridTable" v-loading="loading">
                    <tr>
                        <td class="label">起始号 ：</td>
                        <td>
                            <span>{{info.quotaInvoiceStart+ " - " +info.quotaInvoiceEnd}}</span>
                        </td>
                        <td class="label">票面金额 ：</td>
                        <td>
                            <span>{{info.quotaInvoiceAmount}}</span>
                        </td>
                    </tr>
                </table>
                <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}"
                        border size="mini">
                    <el-table-column align="center" label="编号" prop="quotaInvoiceNum" show-overflow-tooltip/>
                    <el-table-column align="center" label="状态" prop="quotaStatus" show-overflow-tooltip>
                         <template slot-scope="scope">
                            {{scope.row.quotaStatus | doneStatus}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="银行名称" prop="turnBank" show-overflow-tooltip/>
                    <el-table-column align="center" label="收据编号" prop="turnReceiptNum" show-overflow-tooltip/>
                    <el-table-column align="center" label="银行缴纳日期" prop="turnDay" show-overflow-tooltip>
                        <template slot-scope="scope">
                            {{scope.row.turnDay | formatDate}}
                        </template>
                    </el-table-column>
                </el-table>
                <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total" @getData="initPage"
                       class="page-css"/>
            </div>

        </el-dialog>
    </div>
</template>
<script>
    import LjPagination from '../../../../../components/LjPagination'
    import {apiGetCaseQuotaInvoice} from "../../../../../api/case";
    import {formatDate,quotaStatus,doneStatus} from "../../../../../until/wordBook";

    import md5 from 'js-md5'

    let that;
    export default {
        name: 'see',
        props: {
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: '查看'
            },
            id: {
                type: String,
                default: ''
            },

        },
        components:{
           LjPagination
        },
        data() {
            return {
                loading: false,
                list: [],
                info:{},
                form: {
                    pageSize: 20,
                    pageNum: 1,
                },
                total: 0,
            }
        },
        methods: {
            //关闭弹窗
            closeDialog() {
                this.$emit('update:show', false)
            },

            // 提交
            submitForm() {
                this.closeDialog();
            },
            //初始化
            initPage(){
                if(this.id){
                    this.form.quotaId = this.id;
                    apiGetCaseQuotaInvoice(this.form).then(res => {
                        this.loading = false;
                        this.total = res.data.quotaDetailNum;
                        this.list = res.data.caseQuotaDetailList;
                        this.info = res.data;
                    });
                }
                
            }

        },
        filters:{
            formatDate(value){
                return formatDate(value,"yyyy-MM-dd")
            },
            doneStatus(value){
                return doneStatus[value]
            }
        },
        watch:{
           show(value){
               if(value){
                   this.initPage()
               }
           }
        }
    }
</script>
<style lang='less' scoped>
   .gridTable{
       margin-bottom: 5px
   }
</style>


